import uQRCode from '../../../../components/CTKY/uni-qrcode/uqrcode.js'
export default {
	
	data() {
		return {
			orderID:'',
			role:'过发车时间将无法退票',
			orderInfo:[],//订单数据
			passageInfo:[],
			ticketNum:0,
			qrcodeSrc: '',//二维码
			qrcodeText: 'uQRCode',
			qrcodeSize: 150,
			ticketNumber:'',
		}
	},
	onLoad(res) {
		var that = this;
		var orderInfo = JSON.parse(res.orderInfo);
		that.orderInfo = orderInfo;
		console.log(orderInfo);
		this.ticketNumber = orderInfo.ticketNumber;
		that.stringTurnArray(orderInfo.iDNameType);
		that.getTicketNum(orderInfo);
		that.make(this.orderInfo.ticketNumber);
	},
	methods: {
		//-------------------------------生成二维码-------------------------------
		make(param) {
			if(param) {
				console.log(param);
				uQRCode.make({
					canvasId: 'ctkyQrcode',
					text: param,
					size: this.qrcodeSize,
					margin: 10,
					success: res => {
						// console.log('完成')
						this.qrcodeSrc = res
					},
					complete: () => {
						// uni.hideLoading()
						// console.log('完成')
					}
				})
			}
		},
		//-------------------------------获取乘车人信息-------------------------------
		stringTurnArray(param){
			var that = this;
			console.log(param)
			
			let a = param.indexOf('|')
			var singleArray = [];
			console.log(a)
			if(param.indexOf('|')) {
				//存在'|'
				var array = param.split('|');
				for(let i = 0;i < array.length; i++) {
					singleArray = array[i].split(',');
					var passenger = {
						userName:singleArray[1],
						userCodeNum:singleArray[0],
					}
					that.passageInfo.push(passenger);
				}
			}else {//不存在'|'
				var array = param.split(',');
				var passenger = {
					userName:array[1],
					userCodeNum:array[0],
				}
				that.passageInfo.push(passenger);
				console.log(that.passageInfo)
			}
		},
		//-------------------------------计算车票数量-------------------------------
		getTicketNum(param) {
			return Number(param.fullTicket) + Number(param.halfTicket) + Number(param.carryChild)
		},
		//-------------------------判断订单状态-------------------------
		getCtkyOrderStatus(param) {
			if (param == 4) {
				return '进行中'
			} else if (param == 5) {
				return '已完成'
			} else if (param == 6) {
				return '已退票'
			} else if (param == 7) {
				return '未支付'
			} else if (param == 9) {
				return '已撤销'
			} else if (param == 22) {
				return '已改签'
			}
		},
		//-------------------------判断订单状态-------------------------
		getQRCodeStatus(param) {
			if (param == 6) {//已退票
				return '订单已退票'
			} else if (param == 7) {//未支付
				return '订单未支付'
			} else if (param == 9) {//已撤销
				return '订单已撤销'
			}
		},
	}
}
